header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 1.5rem;
    height: 40px;
  }
  main {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .app-container {
    display: grid;
    height: 100vh;
    grid-template-rows: auto 1fr;
  }
  @media screen and (min-width: 700px) {
    header {
      margin: 37px 40px;
      height: 52px;
    }
  }
  @media screen and (min-width: 800px) {
    header {
      margin: 7.54vh 11.46vw 8.3vh 11.46vw;
    }
    @media screen and (min-width: 1500px) {
      header {
        width: 1110px;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }