.game-over-popup {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--pop-up-overlay-bg);
    transition: 0.75s ease-out;
    overflow-y: scroll;
  }
  .game-over-card {
    max-width: 654px;
    width: 87.2vw;
    padding: 1.5rem;
    border-radius: var(--border-radius-small);
    background-color: var(--card-bg);
  }
  .game-over-card-tittle {
    text-align: center;
    margin-top: 8px;
  }
  .game-over-card-tittle h2 {
    font-size: var(--game-over-h2-font-size);
    line-height: var(--game-over-h2-line-height);
    color: var(--dark-text-color);
    margin-bottom: 9px;
  }
  .game-over-card-tittle p {
    font-size: var(--game-over-sub-font-size);
    line-height: var(--game-over-sub-line-height);
    color: var(--medium-text-color);
  }
  .game-over-player-info {
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
  }
  .game-over-player-info div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    border-radius: var(--border-radius-xsmall);
    background-color: var(--player-info-bg);
  }
  .game-over-player-info h3 {
    font-size: var(--game-over-title-font-size);
    line-height: var(--game-over-title-line-height);
    color: var(--medium-text-color);
  }
  .game-over-player-info p {
    font-size: var(--game-over-p-font-size);
    line-height: var(--game-over-p-line-height);
    color: var(--medium-dark-text-color);
  }
  .game-over-button-container {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  .game-over-button-container button {
    padding: 14px;
    font-size: var(--button-font-size);
    color: var(--light-text-color);
    background-color: var(--primary-button-bg);
  }
  .game-over-button-container button:hover {
    background-color: var(--primary-button-bg-hover);
  }
  .game-over-button-container button:nth-child(2) {
    color: var(--medium-dark-text-color);
    background-color: var(--player-info-bg);
  }
  .finalscore.winner {
    color: var(--light-text-color);
    background-color: var(--dark-text-color);
  }
  .finalscore.winner h3 {
    color: var(--light-text-color);
  }
  .finalscore.winner p {
    color: var(--light-text-color);
  }
  @media screen and (min-width: 700px) {
    .game-over-card {
      width: 85.15vw;
      padding: 51px 56px 69px 51px;
      border-radius: var(--border-radius-medium);
      background-color: var(--card-bg);
    }
    .game-over-card-tittle {
      margin-top: 0px;
    }
    .game-over-card-tittle h2 {
      font-size: var(--game-over-h2-font-size-tablet-dsk);
      line-height: var(--game-over-h2-line-height-tablet-dsk);
      margin-bottom: 16px;
    }
    .game-over-card-tittle p {
      font-size: var(--game-over-sub-font-size-tablet-dsk);
      line-height: var(--game-over-sub-line-height-tablet-dsk);
    }
    .game-over-player-info {
      gap: 16px;
      margin-top: 40px;
      margin-bottom: 40px;
    }
    .game-over-player-info div {
      border-radius: var(--border-radius-small);
      padding: 25px 32px;
    }
    .game-over-player-info h3 {
      font-size: var(--game-over-title-font-size-tablet-dsk);
      line-height: var(--game-over-title-line-height-tablet-dsk);
    }
    .game-over-player-info p {
      font-size: var(--game-over-p-font-size-tablet-dsk);
      line-height: var(--game-over-p-line-height-tablet-dsk);
    }
    .game-over-button-container {
      flex-direction: row;
      gap: 14px;
    }
    .game-over-button-container button {
      flex-grow: 1;
      flex-basis: 50%;
      padding: 13.5px;
      font-size: 20px;
      line-height: 25px;
    }
  }
  @media screen and (min-width: 800px) {
    .game-over-card {
      width: 45.42vw;
      padding: 51px 56px 69px 56px;
    }
  }